@charset "UTF-8";
body,
button,
input,
select,
textarea {
  font-family: "PTSans-Regular";
  color: #496e86;
  font-size: 16px; }

.display-flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.logo-img {
  width: 359px;
  height: 84px; }

.meta {
  display: flex;
  justify-content: space-around;
  padding: 10px 0;
  font-family: "FontAwesome";
  color: #7bbfec;
  font-size: 14px; }
  .meta.meta-white {
    justify-content: flex-start; }
    .meta.meta-white .messag:before {
      margin-left: 20px; }
  .meta-span {
    content: "";
    display: inline-block;
    font-family: "FontAwesome";
    color: #7bbfec;
    font-size: 14px; }
    .meta-span.white {
      color: #ffffff; }
  .meta .views:before {
    content: "\f06e";
    padding-right: 5px; }
  .meta .messag:before {
    content: "\f27a";
    padding-right: 5px; }

.soclink {
  font-family: "FontAwesome";
  color: #ffffff;
  font-size: 16px;
  padding: 7px; }
  .soclink:hover {
    color: #31cf87;
    background-color: #ffffff;
    border-radius: 5px; }

.title-articl {
  width: 100%;
  font-family: "PTSerif-Bold";
  color: #496e86;
  font-size: 20px;
  padding: 0 0 20px 0; }
  .title-articl:after {
    content: "";
    display: block;
    width: 20px;
    border: 1px solid #ffffff;
    margin-top: 20px; }
  .title-articl.right {
    display: inline-block;
    padding: 20px 0 0 70px; }
  .title-articl:after {
    border-color: #7bbfec; }

.title-main {
  font-family: font-serifbold;
  color: #7bbfec;
  font-size: 24px;
  text-align: center;
  position: relative;
  font-weight: 700; }
  .title-main:after {
    content: "";
    display: block;
    width: 200px;
    height: 3px;
    border-radius: 50%;
    background: rgba(29, 82, 115, 0.3);
    background-image: -webkit-linear-gradient(left, #fff, rgba(29, 82, 115, 0.3), #fff);
    background-image: -moz-linear-gradient(left, #fff, rgba(29, 82, 115, 0.3), #fff);
    background-image: -ms-linear-gradient(left, #fff, rgba(29, 82, 115, 0.3), #fff);
    background-image: -o-linear-gradient(left, #fff, rgba(29, 82, 115, 0.3), #fff);
    margin: 10px auto 0 auto; }

.items-title {
  font-family: "PTSans-Regular";
  color: #00d381;
  font-size: 14px; }
  .items-title.white {
    color: #ffffff; }
    .items-title.white:after {
      margin: 20px 0; }
  .items-title:after {
    content: "";
    display: block;
    width: 20px;
    border: 1px solid #00d381;
    margin: 7px auto; }

.items-content {
  text-align: center;
  padding: 10px 20px; }
  .items-content .items-title-articl {
    font-family: "PTSans-Regular";
    color: #496e86;
    font-size: 14px; }

.items {
  flex-basis: calc(100%/4 - 20px);
  line-height: 16px; }

.row-heading {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  border: 1px solid #f1f9fd;
  background-color: #f1f9fd;
  padding: 40px; }

.heading-item {
  height: 155px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  position: relative;
  z-index: 1;
  background-image: url(/images/background/pop1.jpeg);
  background-position: center;
  background-size: cover;
  font-family: "PTSerif-Regular";
  color: #1d5273;
  font-size: 16px;
  margin: 0 auto;
  padding: 0 18px;
  text-align: center;
  box-sizing: border-box;
  transition: .5s; }
  .heading-item:after {
    content: "";
    display: block;
    width: 20px;
    border: 1px solid #ffffff;
    margin-top: 20px; }
  .heading-item:after {
    border-color: #1d5273;
    margin-top: 15px; }
  .heading-item:hover {
    background-color: #00d381;
    background-image: none;
    color: #00d381; }
    .heading-item:hover:after {
      border-color: #00d381; }
  .heading-item:before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 50%;
    margin: 10px;
    z-index: -1; }

.pub-picture {
  width: 70px;
  height: 70px;
  margin-right: 20px; }

.link-piblicat {
  display: block;
  color: #496e86;
  padding: 5px 5px 15px 5px; }
  .link-piblicat:hover {
    color: #77c1ee; }

.pub-link {
  color: #7cc0ed; }
  .pub-link:hover {
    color: #2aa1f0; }

.wp-pagenavi {
  width: 100%;
  text-align: center;
  margin: 40px 0; }
  .wp-pagenavi .link-navigation {
    display: inline-block;
    min-width: 34px;
    font-size: 14px;
    border-radius: 5px;
    border: 1px solid #cde7f8;
    padding: 5px 0;
    text-align: center;
    margin: 0 3px; }
    .wp-pagenavi .link-navigation.active {
      background-color: #cde7f8; }
    .wp-pagenavi .link-navigation:hover {
      background-color: #edf2f6; }
  .wp-pagenavi .dott {
    margin: 0 2px; }

.search-form {
  display: flex;
  width: 250px;
  flex-wrap: wrap; }
  .search-form .searchstring {
    display: inline-block;
    width: calc(100% - 55px);
    height: 36px;
    padding-left: 10px;
    border: 1px solid #eff8fd;
    background-color: #eff8fd;
    border-radius: 0;
    font-family: "PTSans-Regular";
    color: rgba(73, 110, 134, 0.5);
    font-size: 14px; }
    .search-form .searchstring::-moz-placeholder {
      font-family: "PTSans-Regular";
      color: rgba(73, 110, 134, 0.5);
      font-size: 14px;
      opacity: 1; }
    .search-form .searchstring::-webkit-input-placeholder {
      font-family: "PTSans-Regular";
      color: rgba(73, 110, 134, 0.5);
      font-size: 14px;
      opacity: 1; }

.searchsubmit {
  display: inline-block;
  width: 40px;
  height: 40px;
  font-family: "FontAwesome";
  color: #ffffff;
  font-size: 14px;
  vertical-align: top;
  border: 1px solid #eff8fd;
  background-color: #7cc0ed;
  border-radius: 0;
  cursor: pointer; }

.sign-search {
  font-family: "PTSans-Regular";
  color: #ffffff;
  font-size: 12px;
  padding-top: 5px; }

header {
  background-color: #80b9e6;
  background-image: url(/images/background/image_heder.jpg);
  background-repeat: no-repeat;
  background-size: cover; }

.wrapper-header {
  display: flex;
  flex-wrap: wrap;
  width: 1060px;
  margin: 50px auto;
  margin: 0 auto; }

.header-body {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding: 30px 0; }
  .header-body .menu-header-top {
    display: flex; }
    .header-body .menu-header-top ul.menu-top {
      padding: 0 20px;
      text-align: center; }
      .header-body .menu-header-top ul.menu-top a {
        display: inline-block;
        font-family: "PTSans-Regular";
        color: #ffffff;
        font-size: 14px;
        padding-bottom: 5px;
        transition: 1s;
        position: relative; }
        .header-body .menu-header-top ul.menu-top a:after {
          content: '';
          position: absolute;
          top: 100%;
          left: 0;
          width: 100%;
          height: 1px;
          background: #ffffff;
          opacity: 0;
          transition: opacity .3s,transform .3s;
          transform: translateY(10px); }
        .header-body .menu-header-top ul.menu-top a:hover:after {
          opacity: 1;
          transform: translateY(-4px); }

.main-navigation {
  width: 100%;
  background-image: url(/images/background/pattern_blue.png); }
  .main-navigation ul.menu {
    display: flex;
    width: 1060px;
    margin: 0 auto; }
    .main-navigation ul.menu li.menu-item {
      transition: all 0.3s;
      position: relative; }
      .main-navigation ul.menu li.menu-item:hover .sub-menu {
        display: block; }
      .main-navigation ul.menu li.menu-item:after {
        content: '•';
        display: inline-block;
        vertical-align: middle;
        font-family: "PTSans-Regular";
        color: #ffffff;
        font-size: 12px; }
      .main-navigation ul.menu li.menu-item:hover:after {
        color: transparent; }
      .main-navigation ul.menu li.menu-item:hover {
        background-color: #7cc0ed; }
      .main-navigation ul.menu li.menu-item:last-of-type:after {
        color: transparent; }
    .main-navigation ul.menu a {
      display: inline-block;
      font-family: "PTSans-Regular";
      color: #ffffff;
      font-size: 16px;
      padding: 18px 15px; }
    .main-navigation ul.menu ul.sub-menu {
      display: none;
      min-width: 230px;
      background-color: #7cc0ed;
      position: absolute;
      top: 100%;
      margin: 0;
      z-index: 2; }
      .main-navigation ul.menu ul.sub-menu li.menu-item:hover {
        background-color: #2f8fc8; }
        .main-navigation ul.menu ul.sub-menu li.menu-item:hover > ul.sub-menu.second {
          display: block; }
      .main-navigation ul.menu ul.sub-menu li.menu-item::after {
        font-size: 0; }
      .main-navigation ul.menu ul.sub-menu ul.sub-menu.second {
        display: none;
        left: 100%;
        top: 0; }

